.contact-card-container {
  min-width: 400px;
  max-width: 400px;
  min-height: 350px;
  margin: 10px;
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #000;
}

.user-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin-bottom: 1.5em;
  font-size: 1.1em;
}

.user-round {
  width: 200px;
  height: 200px;
  margin-bottom: 1em;
  border: 1px solid #555;
  border-radius: 50%;
  /*display: flex;
  align-items: center;
  justify-content: center;*/
  font-size: 1.5em;
  position: relative;
}

.user-round p {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  word-break: break-all;
  width: 75%;
}

.contact-details-link {
  align-self: end;
  margin-right: 1em;
}
